@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html {
    font-family: "Roboto", sans-serif;
}

a {
    text-decoration: none;
}

header{background-color:#ededed;height:8rem;width:100%;display:grid;grid-template-columns:10fr 1fr;align-items:center;position:fixed;top:0}header .title{position:relative;padding-left:2vw}header .title span{font-size:2.6rem}header button{padding:0.7rem 0;width:8rem;cursor:pointer;background-color:white;border:1px solid #d0d0d0;border-radius:5px;transition:0.3s;display:flex;align-items:center;justify-content:center}header button:hover{background-color:#f2f1f1}header button svg{width:1rem;margin-right:5px;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}

.product{border:1px solid #d0d0d0;border-radius:5px;padding:1rem}.product .title{font-size:1.5rem;margin-bottom:1.5rem}.product img{border-radius:5px;margin-bottom:1.5rem;max-width:100%;max-height:22rem}.product .price{margin-top:1.5rem;font-size:1.2rem;color:red;font-weight:bold}.product .stock{font-style:italic;font-size:0.9rem}.product .group-button{display:grid;grid-template-columns:1fr 1fr;justify-items:center}.product .group-button Button{padding:0.7rem 0;display:flex;align-items:center;justify-content:center;cursor:pointer;width:10rem;border:1px solid #d0d0d0;border-radius:5px;color:white;margin-top:1.5rem;transition:0.3s}.product .group-button Button:hover{opacity:0.8}.product .group-button Button:nth-child(1){background-color:#407bca}.product .group-button Button:nth-child(2){background-color:#f7b247}.product .group-button Button svg{width:1rem;margin-right:0.4rem;fill:white}

.container{margin-top:8rem;display:grid;padding:3rem;width:100%;grid-template-columns:repeat(auto-fit, 25rem);grid-gap:3rem}

.detail-container{grid-template-columns:1fr 1fr;display:grid;margin-top:10rem}.detail-container img{max-width:100%;max-height:600px;margin:auto}.detail-container .information .title{font-size:2rem}.detail-container .information p .attribute{font-weight:bold}.detail-container .information p .fill{background-color:#f7b247;padding:3px 6px;border-radius:4px;color:white;font-weight:bold}.detail-container .information .price{font-size:1.2rem}.detail-container .information .button-group{display:flex}.detail-container .information .button-group button{padding:0.5rem 1rem;min-width:7.5rem;display:flex;align-items:center;justify-content:center;margin-right:1rem;color:white;border:none;border-radius:5px;cursor:pointer;transition:0.3s}.detail-container .information .button-group button:hover{opacity:0.8}.detail-container .information .button-group button svg{width:1.1rem;fill:white;margin-right:5px}.detail-container .information .button-group .back{background-color:#00c960}.detail-container .information .button-group .order{background-color:#f7b247}.detail-container .information .button-group .order svg{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}

.alert-box{padding-top:1.5rem;padding-left:2rem;font-size:1.2rem;color:red}.login-form{margin:auto;margin-top:15rem;width:30vw;min-width:20rem;border-radius:5px;border:1px solid #d0d0d0}.login-form .title{background-color:#f5f5f5;padding:1rem;border-bottom:1px solid #d0d0d0;font-size:1.2rem}.login-form form{width:100%;padding:2rem}.login-form form input{width:100%;border:1px solid #d0d0d0;border-radius:5px;height:3rem;padding-left:1vw}.login-form form input:nth-child(2){margin-top:1rem}.login-form form button{background-color:#00c960;width:100%;padding:0.8rem 0;border:none;border-radius:5px;cursor:pointer;margin-top:1.5rem;color:white;font-size:1.2rem;transition:0.3s}.login-form form button:hover{background-color:#00b658}

.add-header{background-color:#ededed;height:8rem;width:100%;display:grid;grid-template-columns:10fr 1fr;align-items:center}.add-header .title{position:relative;padding-left:2vw}.add-header .title span{font-size:2.6rem}.add-header button{padding:0.5rem 0;width:5rem;cursor:pointer;background-color:#eb3946;border:none;border-radius:5px;transition:0.3s;display:flex;align-items:center;justify-content:center;color:white}.add-header button:hover{background-color:#c4232e}.add-container{padding:2rem 4vw;margin-top:8rem}.add-container .title{font-size:1.5rem;border-bottom:1px solid #d0d0d0;margin-bottom:3rem}.add-container form{display:grid;grid-template-columns:1fr 5fr;grid-gap:calc(0.9rem + 1vw);align-items:center}.add-container form label{text-align:right;font-weight:bold}.add-container form input,.add-container form textarea{height:2rem;width:calc(13rem + 15vw);border-radius:5px;border:1px solid #d0d0d0;padding-left:0.5vw}.add-container form textarea{padding:1vh;height:6rem}.add-container form .condition-input{display:flex;align-items:center}.add-container form .condition-input label{text-align:center;font-weight:normal;margin-right:15px;margin-left:3px;cursor:pointer}.add-container form .condition-input input{width:1rem;cursor:pointer}.add-container form input[type="file"]{border:none;cursor:pointer}.add-container form button{grid-column:2/3;width:7rem;padding:0.6rem 0;background-color:#407bca;color:white;border:none;border-radius:3px;margin-left:2vw;cursor:pointer;transition:0.3s}.add-container form button:hover{opacity:0.8}

